const   OVERTIME_GET_LIST           = '/api/overtimes/get-list'
const   CONFIRM_OVERTIME            = '/api/overtimes/confirm-overtime'
const   OVERTIME_SAVE               = '/api/overtimes/save'
const   OVERTIME_UPDATE            = '/api/overtimes/update'
const   OVERTIME_DELETE            = '/api/overtimes/delete'



export default {
    getList(axios, conditions, success, fail) {
        let query = {
            params: conditions
        }
        axios.get(OVERTIME_GET_LIST, query)
            .then((response) => {
                let dataJson = response.data
                success(dataJson)
            })
            .catch((error) => {
                fail(error)
            })
    },
    save(axios, form, success, fail) {
        axios.post(OVERTIME_SAVE, form)
            .then((response) => {
                let data = response.data
                success(data)
            })
            .catch((error) => {
                fail(error)
            })
    },
    update(axios, form, success, fail) {
        axios.post(OVERTIME_UPDATE, form)
            .then((response) => {
                let data = response.data
                success(data)
            })
            .catch((error) => {
                fail(error)
            })
    },
    confirmOvertime(axios, form, success, fail) {
        axios.post(CONFIRM_OVERTIME, form)
            .then((response) => {
                let dataJson = response.data
                success(dataJson)
            })
            .catch((error) => {
                fail(error)
            })
    },
    delete(axios, form, success, fail) {
        axios.post(OVERTIME_DELETE, form)
        .then((response) => {
            let dataJson = response.data
            success(dataJson)
        })
        .catch((error) => {
            fail(error)
        })
    },
   

   
   
   
   
}