<template>
    <div id="pcm-issues-view">
        <div class="row">
            <div class="col-md-2">
                <el-date-picker
                    v-model="form.filter.date"
                    class="w-100"
                    type="month"
                    format="MM/yyyy"
                    value-format="yyyy-MM"
                    :clearable="false"
                    @change="loadData()"
                    :picker-options="pickerOptions"
                    >
                </el-date-picker>
            </div>
            <div class="col-md-1">
                <el-button type="success" @click="resetFormOvertime(); dialogVisible.overtime = true ; dialogVisible.disable = false; dialogVisible.editOT = false ">Thêm mới</el-button>
            </div>
        </div>
        <div v-loading="loadingState.overtimeList">
            <template>
                <el-table :data="overtimes.list" style="width: 100%">
                    <el-table-column label="Ngày OT" width="150">
                        <template slot-scope="scope">
                            <span class="badge badge-heading"
                            :class="{'badge-success': scope.row.status == 1, 'badge-secondary': scope.row.status == 0,'badge-danger': scope.row.status == -1}"
                            style="font-size: 100%">
                                {{scope.row.check_date | formatDate('ddd, DD/MM')}}

                            </span>
                        </template>
                    </el-table-column>

                    <el-table-column label="Tên" v-if="$acl.check('isAdmin')">
                        <template slot-scope="scope">
                            <div class="d-flex">
                                <div class="user-item-name">
                                    <span class="font-weight-bold">
                                        {{scope.row.user.full_name}}
                                    </span>
                                    <br>
                                    <span>{{ scope.row.user.email }}</span>
                                </div>
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column label="Dự án">
                        <template slot-scope="scope" >
                            <div class="d-flex">
                                <div class="project-item-name">
                                    <span class="font-weight-bold">
                                        {{scope.row.project.name}}
                                    </span>
                                </div>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column  label="Bắt đầu"  width="200">
                        <template slot-scope="scope">
                            <span class="font-weight-bold">{{scope.row.start_time | formatOvertime(scope.row.check_date) }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="Kết thúc" width="200">
                        <template slot-scope="scope">
                            <span class="font-weight-bold">{{scope.row.end_time | formatOvertime(scope.row.start_time) }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="Thời gian" width="150">
                        <template slot-scope="scope">
                            <span class="font-weight-bold">{{scope.row.ot_hour | formatHourOT(scope.row.ot_minute) }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="work_rate" label="Hệ số" width="80"> 

                    </el-table-column>

                    <el-table-column label="#" width="230">
                        <template slot-scope="scope">
                            <i v-if="scope.row.id > 0 && scope.row.status == OvertimeModel.status.ACCEPT" class="fas fa-check-circle fa-lg fa-fw text-success"></i>
                            <span v-if="scope.row.id > 0 && scope.row.status == OvertimeModel.status.DENY">
                                <i class="fas fa-times-circle fa-lg fa-fw text-danger"></i>
                                <span v-if="scope.row.comment != null">
                                    <br>    
                                    <span class="small">{{scope.row.comment}}</span>
                                </span>
                            </span>
                            <span v-if="scope.row.status == OvertimeModel.status.WAITING" >
                                <el-button v-if="$acl.check('isAdmin')" size="mini" @click="acceptRequest(scope.row)" type="primary">Xác nhận</el-button>
                                <el-button v-if="scope.row.user_id == $store.state.app_user.user.id" size="mini" @click="dialogVisible.disable = false; editOvertime(scope.row)">Sửa</el-button>
                                <el-button v-if="scope.row.user_id == $store.state.app_user.user.id" type="danger" size="mini" @click="deleteOvertime(scope.row.id)">Xóa</el-button>
                            </span>
                        </template>
                    </el-table-column>
                </el-table>
            </template>
        </div>

        <form role="form" @submit.prevent="submitFormOvertime">
            <el-dialog title="Thông tin Overtime"
                       custom-class="el-dialog-body-gray"
                       :visible.sync="dialogVisible.overtime" 
                       width="500px"
            >
                <div v-loading="loadingState.overtimeForm">
                    <div class="row form-group">
                        <div class="col-sm-12">
                            <label class="required">Bắt đầu / Kết thúc</label>
                            <div v-if="dialogVisible.editOT == false">
                                <el-date-picker 
                                    v-model="form.overtime.dates"
                                    v-validate="'required'" 
                                    data-vv-name="dates"
                                    data-vv-as="Dates"  
                                    type="datetimerange"
                                    time-arrow-control
                                    format="dd/MM/yyyy HH:mm"
                                    value-format="yyyy-MM-dd HH:mm"
                                    :class="{'is-invalid': errors.has('dates')}"
                                    :default-time="[_.find(this.getSettings.data, {'setting_key': 'WORKING_START_TIME'}).setting_value + ':00', _.find(this.getSettings.data, {'setting_key': 'WORKING_END_TIME'}).setting_value + ':00']"
                                    >
                                </el-date-picker>
                            </div>
                            <div v-else>
                                <el-time-picker
                                    is-range
                                    v-model="form.overtime.dates"
                                    range-separator="-"
                                    start-placeholder="Giờ bắt đầu"
                                    end-placeholder="Giờ kết thúc"
                                    format="HH:mm"
                                    value-format="yyyy-MM-dd HH:mm"
                                    >
                                </el-time-picker>
                            </div>

                        </div>
                    </div> 
                     <div class="row form-group">
                         <div class="col-sm-12">
                            <label class="required">Dự án</label>
                            <div>
                                <el-select v-model="form.overtime.project_id" placeholder="Chọn dự án">
                                    <el-option 
                                        v-for="items in overtimes.projectList"
                                        :key="items.value" 
                                        :label="items.project.name"
                                        :value="items.project.id">
                                    </el-option>

                                </el-select>
                            </div>
                        </div>
                     </div> 
                    <div class="row form-group">
                        <div class="col-sm-12">
                            <label>Ghi chú</label>
                            <div>
                                <el-input 
                                    :disabled="dialogVisible.disable"
                                    v-model="form.overtime.note"
                                />
                            </div>
                        </div>
                    </div>
                    <div class="row form-group" v-if="dialogVisible.disable == true">
                        <div class="col-sm-12">
                            <label>Comment</label>
                            <div>
                                <el-input 
                                    v-model="form.overtime.comment"
                                />
                            </div>
                        </div>
                    </div>
                    <div class="row form-group" v-if="dialogVisible.disable == true">
                        <div class="col-sm-12">
                            <label class="required">Trạng thái</label>
                            <div>
                                <el-select v-model="form.overtime.status" placeholder="Lựa chọn">
                                    <el-option
                                        v-for="item in OvertimeModel.getOptionStatus()"
                                        :key="item.key"
                                        :label="item.value"
                                        :value="item.key"
                                         
                                    />
                                </el-select>
                            </div>
                        </div>
                    </div>
                      
                </div>
                <div slot="footer" class="dialog-footer d-flex justify-content-between">
                    <button type="submit" class="btn btn-sm btn-primary btn-icon-split"  :disabled="loadingState.overtimeForm">
                        <span class="icon text-white-50">
                            <i class="fas fa-save"></i>
                        </span>
                        <span class="text">Lưu</span>
                    </button>
                </div>
            </el-dialog>

        </form>
        
        
        
    </div>
</template>

<script>

import moment from 'moment'
import API_OVERTIME from '@/utils/api/overtime'
import UserModel from '@/model/user'
import OvertimeModel from '@/model/overtime'
import Avatar from 'vue-avatar'
import _ from 'lodash'
import {
    mapGetters
} from 'vuex'



export default {
    name: 'pcm-overtime-view',
     components: {
        Avatar
    },
    data() {
        return {
            loadingState: {
                overtimeList: false,
                overtimeForm: false
            },
            dialogVisible: {
                overtime: false,
                disable: false,
                editOT: true,
            },
            pickerOptions: {
                disabledDate(time) {
                return time.getTime() > Date.now();
                }
            },
            UserModel: UserModel,
            OvertimeModel : OvertimeModel,
            form: {
                filter: {
                    user_id: this.$store.state.app_user.user.id,
                    date: moment().format('YYYY-MM'),
                    
                },
                overtime: {
                    project_id: '',
                    dates: [],
                    note:'',
                    comment:'',
                    
                }
            },
            overtimes: {
                 list: [],
                 projectList: [],
            },
          
        }
    },
    computed: {
        ...mapGetters([
            'getSettings'
        ]),
        _() {
            return _
        }
    },
    watch: {
        initData: function() {
            if (this.initData) {
                if (this.initData.id > 0) {
                    this.form.attendance.dates = [
                        moment(this.initData.check_in).format('YYYY-MM-DD HH:mm'),
                        this.initData.dates + ' ' + _.find(this.getSettings.data, {'setting_key': 'WORKING_END_TIME'}).setting_value
                    ]
                } else {
                    this.form.attendance.dates = [
                        this.initData.dates + ' ' + _.find(this.getSettings.data, {'setting_key': 'WORKING_START_TIME'}).setting_value,
                        this.initData.dates + ' ' + _.find(this.getSettings.data, {'setting_key': 'WORKING_END_TIME'}).setting_value
                    ]
                }
            }
        }
    },
    created() {
        let self = this;
        this.$emit('activeMenu', 'project.index');
        self.loadData()
        
    },
    methods: {
        loadData() {
            let self = this
            self.loadingState.overtimeList = true
            API_OVERTIME.getList(self.$axios, self.form.filter, function(data){
                self.loadingState.overtimeList = false
                if (data.status) {
                    self.overtimes = data.data
                }
            }, function(error){
                 self.loadingState.overtimeList = false
            })
        },
        acceptRequest(row) {
            let self = this
            self.editOvertime(row)
            self.dialogVisible.disable = true
            
        },
        editOvertime(row){
            let self = this
            self.dialogVisible.editOT = true
            if (self.form.overtime.id == row.id) {
                return self.dialogVisible.overtime = true
            }
            self.resetFormOvertime()
            self.form.overtime.id = row.id
            self.form.overtime.note = row.note
            self.form.overtime.project_id = row.project.id
            self.form.overtime.dates = [
                    moment(row.start_time).format('YYYY-MM-DD HH:mm'),
                    moment(row.end_time).format('YYYY-MM-DD HH:mm')
                ]
            self.dialogVisible.overtime = true
        }
        ,
         submitFormOvertime(){
           let self = this
            self.$validator.validateAll().then((result) => {
                if (result) {
                    self.loadingState.overtimeForm = true
                    if (self.form.overtime.id == 0) {
                      API_OVERTIME.save(self.$axios, self.form.overtime, function (data) {
                            self.loadingState.overtimeForm = false
                            if (data.status) {
                                self.showMessage('success', 'Thêm mới OT thành công!')
                                self.resetFormOvertime()
                                self.dialogVisible.overtime = false
                                self.loadData()
                            } else {
                                if (data.error_code == 103) {
                                    self.showInValidMessage(data.data)
                                }
                            }
                        }, function (error) {
                            self.loadingState.overtimeForm = false
                        })
                    } else {
                        if(self.dialogVisible.disable == true){
                            API_OVERTIME.confirmOvertime(self.$axios, self.form.overtime, function (data) {
                                self.loadingState.overtimeForm = false
                                if (data.status) {
                                    self.showMessage('success', 'Xác nhận OT thành công!')
                                    self.resetFormOvertime()
                                    self.dialogVisible.overtime = false
                                    self.loadData()
                                } else {
                                    if (data.error_code == 103) {
                                        self.showInValidMessage(data.data)
                                    }
                                }
                            }, function (error) {
                                self.loadingState.overtimeForm = false
                            })
                        } else{
                            API_OVERTIME.update(self.$axios, self.form.overtime, function (data) {
                                self.loadingState.overtimeForm = false
                                if (data.status) {
                                    self.showMessage('success', 'Chỉnh sửa OT thành công!')
                                    self.resetFormOvertime()
                                    self.dialogVisible.overtime = false
                                    self.loadData()
                                } else {
                                    if (data.error_code == 103) {
                                        self.showInValidMessage(data.data)
                                    }
                                }
                            }, function (error) {
                                self.loadingState.overtimeForm = false
                            })

                        }
                        
                    } 
                }
            })

        },
        deleteOvertime(id){
            let self = this
            let content = 'Bạn có chắc chắn muốn xóa'
            this.showConfirmLoadingMessage('warning', 'Xóa Overtime', content, (action, instance, done) => {
                if (action === 'confirm') {
                    instance.confirmButtonLoading = true
                    let form = {
                        id: id
                    }
                    API_OVERTIME.delete(self.$axios, form, (data) => {
                        instance.confirmButtonLoading = false
                        if (data.status) {
                            self.showMessage('success', 'Xóa thành công !')
                            done()
                            self.loadData()
                        }
                    }, (error) => {
                        instance.confirmButtonLoading = false
                    })
                } else {
                    done();
                }
            })

        },
        resetFormOvertime() {
            this.$validator.reset()
            this.form.overtime = {
                    id: 0,
                    project_id: '',
                    dates: [],
                    note:'',
                    comment:'',
                    status:'',
                   
                }
        },
         
     
    }
}
</script>