export default {
    status: {
        ACCEPT: 1,
        WAITING : 0,
        DENY  : -1
    },
    getOptionStatus() {
        return [
            {
                key: this.status.ACCEPT,
                value: 'Chấp nhận'
            },
            {
                key: this.status.DENY,
                value: 'Từ chối'
            }
            
        ]
    },

}